.card {
  font-weight: 400;
  font-size: 11px;
  width: 200px;
  padding: 15px 14px;
  box-sizing: border-box;

  display: grid;
  grid-template-rows: repeat(auto-fit, 1fr);
  justify-content: space-between;

  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 auto;
    padding: 12px 0;

    transform: scale(1, 1);
    border: 3px solid transparent;
    transition: transform 1s, border 1s;

    &:hover {
      transform: scale(1.2, 1.2);
      border-color: #f3f3f3;
    }
  }

  &__description {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;

    margin: 30px 0 0;
  }

  &__id {
    margin: 5px 0 0;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: fit-content(50%) fit-content(50%);
    justify-content: space-between;
  }

  &__rating {
    display: grid;
    grid-gap: 4px;
    grid-template-columns: repeat(5, 15px);
    grid-template-rows: 15px;
    justify-content: start;

    margin-top: 15px;

    @for $i from 0 through 5 {
      &--#{$i} {
        & :nth-child(-n + #{$i}) {
          background: url(../images/star-active.svg) no-repeat center;
        }
      }
    }
  }

  &__star {
    background: url(../images/star.svg) no-repeat center;
    width: 15px;
    height: 15px;
    margin-right: 4px;
  }

  &__feedback {
    margin-top: 16px;
    margin-bottom: 0;
    line-height: 13px;
    text-align: right;
  }

  &__price {
    margin-top: 25px;
    margin-bottom: 21px;
    line-height: 13px;
  }

  &__sum {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;

    margin-top: 20px;
    margin-bottom: 20px;
  }

  &__link {
    display: grid;
    justify-content: center;
    line-height: 40px;
    width: 170px;
    height: 40px;
    margin: 0;

    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;

    color: white;
    background-color: #00acdc;
    border-radius: 5px;

    transition: background-color 1s, color 1s linear;
  }

  &__link:hover {
    color: #00acdc;
    background: #fff;
    border: 1px solid #00acdc;
    box-sizing: border-box;
    border-radius: 5px;
  }
}
